/**
 * The object interface used for the validation
 * @prop validate The method used to validate the input.
 * @prop getMessage The method returning the message to display when the validation fails.
 */
export interface RwcInputValidatorInterface {
  validate: (value: string) => boolean;
  getMessage: () => string;
}

/**
 * The valid types of the input component.
 */
export type InputType = 'text' | 'number' | 'password' | 'email' | 'tel' | 'url';

/**
 * The valid values of the input autocomplete attribute.
 */
export type InputAutoComplete = 'on' | 'off';
